/* App.css */
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #f5f5f5;
}

input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Garantindo que o header não fique fixo */
header {
  position: relative; /* Altere de fixed para relative */
  top: 0;
  width: 100%;
  z-index: 1; /* Mantenha-o acima do conteúdo */
}

/* App container - configurando a altura */
.app-container {
  display: flex;
  flex-direction: column;
}

/* Conteúdo do dashboard */
.dashboard-content {
  flex-grow: 1; /* Faz o conteúdo crescer */
  overflow-y: auto; /* Ativa a rolagem quando o conteúdo exceder a altura */
  min-height: 100vh; /* Garante que ocupe a altura total da viewport */
}

.login-container {
  min-height: 18vh; /* Garante que a altura da página de login ocupe a viewport inteira */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; /* Faz com que a imagem cubra a tela inteira */
  z-index: -1; /* Coloca a imagem de fundo atrás do conteúdo */
}

.login-content {
  z-index: 1; /* Garante que o conteúdo da página fique acima da imagem de fundo */
  position: relative;
}

/* Footer fixado */
.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  z-index: 1000;
}

.MuiTypography-h4 {
  font-weight: bold;
  text-align: center;
}

.MuiButton-containedPrimary {
